<template>
  <Layout activeMenu="roleGroupMgr">

      <table-page v-if="showTable==0" pageName="roleGroupMgr"
                  v-on:itemSelectEvent="itemSelectEvent">
        <Button slot="toolbar" type="info" @click="openPageSet">角色配置</Button>
      </table-page>

      <biz-transfer v-if="showTable==1"
                    :closeTransfer.sync="showTable"
                    :config="transferConfig"></biz-transfer>

  </Layout>
</template>

<script type="text/ecmascript-6">
  import tablePage from "../../components/TablePage.vue"
  import bizTransfer from "../../components/BizTransfer.vue"

  export default {
    components: {
      tablePage,bizTransfer
    },
    data: function() {
      return {
        showTable: 0,
        selectedItems: [],
        currentRoleId: null,
        transferConfig: {}
      }
    },
    methods: {
      itemSelectEvent: function(selection) {
        this.selectedItems = selection;
      },
      _judgeSelected: function(){
        let res = true;
        let len = this.selectedItems.length;
        if(0==len) {
          this.$Modal.warning({content: "请选中一项！"});
          res = false;
        }
        return res;
      },
      openPageSet: function(param){
        if(this._judgeSelected()){
          this.transferConfig = {
            "left": {
              "ds": "roleMgr",
              "valueKey": "name",
              "textKey": "name"
            },
            "right": {
              "ds": "roleGroupMgr",
              "filterKey": "groupId",
              "filterValue": this.selectedItems[0].id,
              "valueKey": "roleName"
            }
          };
          this.showTable = 1;
        };
      }
    }
  }
</script>

<style>
</style>



