<template>
  <div class="biz-transfer">
    <div class="ivu-card-head" style="margin-bottom: 15px">
      <p>权限配置</p>
    </div>
    <Transfer
            :data="data"
            :target-keys="targetKeys"
            @on-change="handleChange"></Transfer>

    <div style="margin-top: 15px">
      <i-button type="text" @click="cancel">取消</i-button>
      <i-button type="primary" @click="handleSave">保存</i-button>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import {DataSource} from "../utils/dataSource.js";
  import DataCenter from "../store/DataCenter.js";


  export default {
    props: ['closeTransfer', "config"],
    data: function() {
      return {
        data: [],
        targetKeys: []
      }
    },
    mounted: function() {
        this._refreshListData(this.config);
    },
    methods: {
      _refreshListData: async function(config){
        let leftConfig = config.left;
        this.leftDS = DataCenter.getDataSource(leftConfig.ds);
        let data = await DataCenter.getPagedByDS(this.leftDS);
        if(data.success) {
          //取出Id
          let dataArr = [];
          for(let i= 0,len=data.result.length; i<len; i++) {
            let item = data.result[i];
            let textKey = leftConfig.textKey;
            let text;
            if('string'==typeof textKey) {
              text = item[textKey];
            }else {
              text = textKey(item); //渲染函数
            }
            dataArr.push({
              "key": item[leftConfig.valueKey],
              "label": text
            });
          }
          this.data = dataArr;
        }
        let rightConfig = config.right;
        this.rightDS = DataCenter.getDataSource(rightConfig.ds);
        let data2 = await DataCenter.getPagedByDS(this.rightDS, null, {id: rightConfig.filterValue});

        if(data2.success) {
          //取出Id
          let targetArr = [];
          let dataArr2 = (data2.result[0])[this.rightDS.schema.child[0]];
          if(dataArr2) {
            for(let j= 0,len=dataArr2.length; j<len; j++) {
              let item2 = dataArr2[j];
              targetArr.push(item2[rightConfig.valueKey]);
            }
            this.targetKeys = targetArr;
          }

        }
      },
      handleChange: function(newTargetKeys) {
        this.targetKeys = newTargetKeys;
      },
      cancel: function(){
        this.$emit('update:closeTransfer', 0);
      },
      handleSave: async function(){
        let appId = DataCenter.getAppId();
        if(!appId) {
          this.$Modal.error({content: "未选中项目!"});
        }else {
          let res = false;
          if(this.targetKeys) {
            let relArr = [];
            for (var i = 0, len = this.targetKeys.length; i < len; i++) {
              let item = this.targetKeys[i];
              relArr.push({
                appId: appId,
                [this.config.right.valueKey]: item
              });
            }
            let response = await this.rightDS.update({id: this.config.right.filterValue, [this.rightDS.schema.child[0]]: relArr}, {id: this.config.right.filterValue});
            let data = await response.json();
            res = data.success;
          }else {
            res = true;
          }
          if(res) {
            this.$Modal.success({content: "保存成功!"});
            this.cancel();
          }else {
            this.$Modal.error({content: "保存失败!"});
          }

        }

      }
    }
  }
</script>

<style>
  .biz-transfer .ivu-transfer .ivu-transfer-list{
    height: 400px;
  }
</style>




